header {
  height: 130vh;
  padding-top: 2rem;
  overflow: hidden;
  margin-bottom: -10rem;
}

.header_container {
  text-align: center;
  height: 100%;
  position: relative;
}

.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

.header_social {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 15rem;
}

.header_social a i {
  font-size: 1.5rem;
}

.header_social::after {
  content: '';
  width: 1px;
  height: 2rem;
  background-color: var(--color-primary);
}

.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 17rem;
  height: 30rem;
  position: absolute;
  left: calc(50% - 8rem);
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 5rem 1.5rem 1.5rem 1.5rem;
}

.me img {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  object-fit: fill;
}

.scroll_down {
  position: absolute;
  right: -2.3rem;
  bottom: 20rem;
  transform: rotate(90deg);
  font-size: 0.9rem;
}

@media screen and (max-width: 1025px) {
  header {
    height: 68vh;
  }
}

@media screen and (max-width: 600px) {
  header {
    height: 110vh;
    margin-bottom: -2rem;
  }

  .header_social,
  .scroll_down {
    display: none;
  }

  .cta .btn {
    padding: 0.7rem 1.2rem;
    font-size: 0.5rem;
    font-weight: bold;
  }
}
